<template>
  <v-row justify="center" class="mt-12 mb-12">
    <v-col
      cols="12"
      sm="10"
      md="8"
      lg="6"
    >
      <v-card id="signupForm" class="rounded-lg" elevation="0">
        <v-form ref="form" action="/post" method="post" enctype="multipart/form-data">
        <v-card-text>
            <div class="inputBoxWrap">
              <span class="formTitle">상품명</span>
              <v-text-field
                ref="pName"
                v-model="pName"
                :rules="[() => !!pName || '상품명은 필수입력 항목입니다.']"
                :error-messages="errorMessages"
                placeholder="상품명"
                required
              ></v-text-field>
            </div>

          <div class="inputBoxWrap">
            <span class="formTitle">상품번호</span>
            <v-text-field
                ref="pNum"
                v-model="pNum"
            ></v-text-field>
          </div>

          <div class="inputBoxWrap">
            <span class="formTitle">카테고리</span>
            <v-select
                ref="pCate"
                v-model="pCate"
                :items="items"
                label="카테고리 선택"
            ></v-select>
          </div>

          <div class="inputBoxWrap">
            <span class="formTitle">사진</span>
            <input
                type="file"
                name="file"
                @change="upload"
            >
          </div>


          <div class="inputBoxWrap">
            <span class="formTitle">금액</span>
            <v-text-field
                v-model="pPrice"
                name="pPrice"
            ></v-text-field>
          </div>

            <div class="inputBoxWrap">
              <span class="formTitle">상세설명</span>
          <v-text-field
            ref="pDesc"
            v-model="pDesc"
            required
          ></v-text-field>
            </div>


          <div class="inputBoxWrap">
            <span class="formTitle">판매가능 수량</span>
            <v-text-field
                v-model="pCnt"
                name="pCnt"
            ></v-text-field>
          </div>

        </v-card-text>
        </v-form>

        <v-card-actions  class="mt-12">
          <v-btn text outlined color="error">
            취소
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
          outlined
            color="success"
            text
          type="button"
            @click="submit"
          >
            상품 등록
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
  export default {
    data: () => ({
      errorMessages: '',
      items: ['Diffuser', 'Perfume'],
      image:null,
      pName: null,
      pCate: null,
      pNum: null,
      pPic: '',
      pPrice: '',
      pCnt: 0,
      pDesc: null,
      formHasErrors: false,
      rules: {
        required: value => !!value || '필수 항목입니다.',
        min: v => v.length >= 8 || 'Min 8 characters',
      },
    }),

    computed: {
      form () {
        return {
          pName: this.pName,
          pCate: this.pCate,
          pNum: this.pNum,
          pPic: this.pPic,
          pPrice: this.pPrice,
          pDesc: this.pDesc,
          pCnt: 0,
        }
      },
    },

    watch: {
      name () {
        this.errorMessages = ''
      }
    },

    methods: {
      upload(e){
        this.image = e.target.files[0];
      },
      submit() {
        const validate = this.$refs.form.validate();
        let that = this;
        let formData=new FormData();
        formData.append("product_name", that.pName);
        formData.append("product_category", that.pCate);
        formData.append("product_num", that.pNum);
        formData.append("file", that.image);
        formData.append("product_price", that.pPrice);
        formData.append("product_desc", that.pDesc);
        formData.append("product_cnt", that.pCnt);

        if (validate) {
          this.$axios({
            method: "post",
            url: 'api/admin/addProduct',
            data: formData,
            headers: { "Content-Type": "multipart/form-data" }
          })
              .then((res) => {
                console.log(res);
                alert("상품 등록 완료");
               this.$router.push('/product');
              })
              .catch(err => {
                console.log(err);
              })
        }
      },
    }
  }
</script>
<style scoped>
formTitle {
        display: block;
        margin: 20px 0;
    }

    .inputBoxWrap {
        margin: 50px 0;
    }
</style>